bytes_array_to_base64(array) {
if (array.length == 0) {
return "";
}
var b64Chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/';
var result = "";

var d3 = array.length % 3;
var endChar = "";
if (d3 == 1) {
var value = array[array.length - 1];
endChar = b64Chars.charAt(value >> 2);
endChar += b64Chars.charAt((value << 4) & 0x3F);
endChar += "==";
} else if (d3 == 2) {
var value1 = array[array.length - 2];
var value2 = array[array.length - 1];
endChar = b64Chars.charAt(value1 >> 2);
endChar += b64Chars.charAt(((value1 << 4) & 0x3F) + (value2 >> 4));
endChar += b64Chars.charAt((value2 << 2) & 0x3F);
endChar += "=";
}

var times = array.length / 3;
var startIndex = 0;

for (var i = 0; i < times - (d3 == 0 ? 0 : 1); i++) {
startIndex = i * 3;

var S1 = array[startIndex + 0];
var S2 = array[startIndex + 1];
var S3 = array[startIndex + 2];

var s1 = b64Chars.charAt(S1 >> 2);
var s2 = b64Chars.charAt(((S1 << 4) & 0x3F) + (S2 >> 4));
var s3 = b64Chars.charAt(((S2 & 0xF) << 2) + (S3 >> 6));
var s4 = b64Chars.charAt(S3 & 0x3F);

result += (s1 + s2 + s3 + s4);
}

return result + endChar;
},
string_to_base64(rawString) {
if (rawString.length == 0) {
return "";
}
var b64Chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/';
var result = "";

var d3 = rawString.length % 3;
var endChar = "";
if (d3 == 1) {
var value = rawString.charCodeAt(rawString.length - 1);
endChar = b64Chars.charAt(value >> 2);
endChar += b64Chars.charAt((value << 4) & 0x3F);
endChar += "==";
} else if (d3 == 2) {
var value1 = rawString.charCodeAt(rawString.length - 2);
var value2 = rawString.charCodeAt(rawString.length - 1);
endChar = b64Chars.charAt(value1 >> 2);
endChar += b64Chars.charAt(((value1 << 4) & 0x3F) + (value2 >> 4));
endChar += b64Chars.charAt((value2 << 2) & 0x3F);
endChar += "=";
}



var times = rawString.length / 3;
var startIndex = 0;

for (var i = 0; i < times - (d3 == 0 ? 0 : 1); i++) {
startIndex = i * 3;

var S1 = rawString.charCodeAt(startIndex + 0);
var S2 = rawString.charCodeAt(startIndex + 1);
var S3 = rawString.charCodeAt(startIndex + 2);


var s1 = b64Chars.charAt(S1 >> 2);
var s2 = b64Chars.charAt(((S1 << 4) & 0x3F) + (S2 >> 4));
var s3 = b64Chars.charAt(((S2 & 0xF) << 2) + (S3 >> 6));
var s4 = b64Chars.charAt(S3 & 0x3F);

result += (s1 + s2 + s3 + s4);
}

return result + endChar;
},
base64decode(base64DecodeChars,str) {
var c1, c2, c3, c4;
var i, len, out;

len = str.length;
i = 0;
out = "";
while(i < len) {
/* c1 */
do {
c1 = base64DecodeChars[str.charCodeAt(i++) & 0xff];
} while(i < len && c1 == -1);
if(c1 == -1)
break;

/* c2 */
do {
c2 = base64DecodeChars[str.charCodeAt(i++) & 0xff];
} while(i < len && c2 == -1);
if(c2 == -1)
break;

out += String.fromCharCode((c1 << 2) | ((c2 & 0x30) >> 4));

/* c3 */
do {
c3 = str.charCodeAt(i++) & 0xff;
if(c3 == 61)
return out;
c3 = base64DecodeChars[c3];
} while(i < len && c3 == -1);
if(c3 == -1)
break;

out += String.fromCharCode(((c2 & 0XF) << 4) | ((c3 & 0x3C) >> 2));

/* c4 */
do {
c4 = str.charCodeAt(i++) & 0xff;
if(c4 == 61)
return out;
c4 = base64DecodeChars[c4];
} while(i < len && c4 == -1);
if(c4 == -1)
break;
out += String.fromCharCode(((c3 & 0x03) << 6) | c4);
}
return out;
},
utf8to16(str) {
var out, i, len, c;
var char2, char3;

out = "";
len = str.length;
i = 0;
while(i < len) {
c = str.charCodeAt(i++);
switch(c >> 4)
{
case 0: case 1: case 2: case 3: case 4: case 5: case 6: case 7:

out += str.charAt(i-1);
break;
case 12: case 13:

char2 = str.charCodeAt(i++);
out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F));
break;
case 14:

char2 = str.charCodeAt(i++);
char3 = str.charCodeAt(i++);
out += String.fromCharCode(((c & 0x0F) << 12) |
((char2 & 0x3F) << 6) |
((char3 & 0x3F) << 0));
break;
}
}
},
wxScan(num){
let deviceCode = null;
let _this = this;
wx.scanQRCode({
needResult: num,// 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
scanType: ["qrCode","barCode"],// 可以指定扫二维码还是一维码，默认二者都有
success: function (res) {

deviceCode = res.resultStr.replace(/\/scan\//,'');
let managerId = localStorage.getItem('md') || tools.getCookie('user_id');
_this.$http.post('/deviceBinding/deviceBindingUser',{managerId:managerId,deviceCode:deviceCode}).then((result) => {
let data = result.data;
if(data.successful && (data.resultCode.code=='SUCCESS')){
_this.showToast = true;
_this.toastMsg = '设备添加成功';
setTimeout(function(){
window.location.reload(true);
},1500)
}else{
_this.showToast = true;
_this.toastMsg = data.resultCode.message;
}
});
}
});
},
}
